
#signupComp{
  $paddingLeft: 6%;
  // background: radial-gradient($color-1st, darken($color-1st, 10%));
  height: 100vh;
  width: 100vw;

  #header{
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    padding: .8em $paddingLeft;
    background: $color-1st;
    z-index: 2;
    
    .icon{
      font-size: 1.3em;
      margin-right: 1em;
      color: inherit;
    }
    
    .label{
      font-size: 1.5em;
      margin: 0;

    }

  }

  #content{
    position: relative;
    width: 100%;
    padding: 0 $paddingLeft;
    padding-top: 2em;
    height: max-content;
    // background: aqua;

    #logo{
      margin-bottom: 4em;
      width: 45vw;
      height: auto;
    }

    #forgotPassword{
      width: 100%;
      text-align: right;

      span{
        font-weight: bold;
      }
    }    
        
    button{
      margin-top: 1em;
    }
  }
} 