.navigation {
    background-color: $color-1st;

    > .ps-container,
    > .container {
        display: flex;
        flex-flow: row nowrap;

        > * {
            width: 100%;
        }
    }

    &__extra {
        > li {
            position: relative;
            display: inline-block;
            margin-right: 20px;
            padding-right: 20px;

            &:after {
                content: '';
                @include vertical-align();
                right: 0;
                width: 2px;
                height: 15px;
                background-color: #000;
            }

            a {
                color: #000000;
            }

            &:last-child {
                margin-right: 0;
                padding-right: 0;

                &:after {
                    display: none;
                }
            }
        }

        .ps-dropdown {
            img {
                margin-right: 8px;
            }
        }
    }

    .navigation__left {
        max-width: 260px;
    }

    .navigation__right {
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: center;
        padding-left: 35px;
    }
}

header#header {
  // @extend %list-reset;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 9999;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 3em calc(100% - 6em) 3em;
  justify-content: space-between;
  width: 100%;
  height: $document-sm-font-size * 4;
  text-align: center;
  border-bottom: 0 none;
  background-color: $color-1st;
  padding: $document-sm-padding-top $document-sm-padding-left;
  // @include clearfix();
  // @include transition(all 0.4s $ease-in-out-quad);

  $icons-size: 1.5em;
  .left, .center, .right{
    display: flex;
    align-items: center;
  }
  
  .left, .right{
    width: 3em;
  }
  
  .center{
    justify-content: center;
    width: 100%;
  }
  
  .right{
    display: flex;
    justify-content: flex-end;
    
    a{
      display: inline-block;
      color: inherit;
      text-decoration: none;
      overflow: hidden;
      border: solid 1px;
      border-radius: 999999px;
      font-size: inherit;
      width: 3em;
      height: 3em;
      
      img{
        width: 100%;
        height: 100%;
        object-position: center;
        object-fit: cover;
        border-radius: 999999px;
      }

      &:active,&:hover,&:focus{
        color: inherit;
        
      }
    }
  }

  .home-icon, .go-back-icon, .user-avi{
    font-size: $icons-size;
  }

  .user-avi{
    display: flex;
    justify-content: center;
    align-items: center;

  }

  .nav-label{
    font-weight: bold;
    font-size: 1.25em;
    text-transform: capitalize;
    white-space: nowrap;
    text-overflow: ellipsis;
    word-wrap: break-word;
    width: 100%;
    overflow: hidden;

  }

  .search-bar{
    width: 65vw;

  }

  ////////////////////////////////////////////////////////////////////////////////////////////////////////

  .navigation__extra {
      display: inline-block;

      li {
          margin-right: 5px;
          padding-right: 5px;

          &:after {
              display: none;
          }
      }
  }

  .header__extra {
      span {
          background-color: $color-1st;
      }
  }

  .header__actions {
      i {
          font-size: 24px;
      }
  }

  .ps-block--user-header {
      i {
          font-size: 24px;
      }
  }

  .header__top {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      background-color: #f4f5f5;
      padding: 15px 30px;

      > * {
          width: 100%;
      }

      .header__left {
          max-width: 40%;

          p {
              margin-bottom: 0;
          }
      }

      .header__right {
          max-width: 60%;
          text-align: right;

          .navigation__extra {
              display: inline-block;
          }
      }
  }

  .navigation--mobile {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      padding: 10px 30px;
      border-bottom: none;

      .navigation__left {
          display: flex;
          align-items: center;
      }
  }

  .header__extra {
      span {
          background-color: #000;

          i {
              color: #ffffff;
              font-weight: 600;
          }
      }
  }

  &.active {
      @include transform(translateX(270px));
  }

  &.header--sticky {
      .navigation--mobile {
          position: fixed;
          top: 0;
          width: 100%;
          z-index: 1000;
          background-color: $color-1st;
          @include box-shadow(0 0 3px 0 rgba(#000, 0.12));
      }
  }

