
#forgotPasswordComp{
  // background: radial-gradient($color-1st, darken($color-1st, 10%));
  height: 100vh;
  width: 100vw;
  padding: $document-sm-padding-left;

  #content{
    position: relative;
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
    // align-items: center;

    #logo{
      margin-bottom: 4em;
      width: 45vw;
      height: auto;
    }

    .input-field{
      margin: 1em 0;
      width: 100%;

      input{
        font-size: 1.1em;
        text-align: center;
      }
      
      &#email{
        input{
          text-align: left;
        }

      }
    }

    #changeEmail{
      width: 100%;
      span{
        color: inherit;
        font-weight: bold;
      }
    }
    
    #resendCode{
      .timer{
        color: #c0c0c0;
        margin-left: 1em;
      }
      
      &.disabled{
        color: #c0c0c0;

      }
    }

    #ctnBtn{
      color: inherit;
      width: 100%;
        
    }
    
  }

} 